import common from '../../../common/common.js'

export default {
		data() {
			return {
				name:'aa',
				userInfo:[],		//用户信息
				scrollLeft:0,
				tabIndex: 0,
				isClickChange:false,
				tabBars: [{
					name: '待配货',
					id: '0'
				}, {
					name: '已配货',
					id: '1'
				}],
				orderList: [],
				dtype:0,			//类型 1:今日  0：全部
				page:1,
				sdate:'',			//日期
				edate:'',			//日期
				orderNums:0,		//订单数量
				orderAmounts:0.00	,//订单金额
				orderJiesuanAmounts:0.00,	//结算金额
				subindex:-1,
			}
		},
		onLoad(option) {
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var utype =  uni.getStorageSync('utype');			//用户类型
			
			var dtype = option.dtype
			that.dtype = dtype
			
			var dateObj = new Date();
			var sdate = dateObj.getFullYear()+"-" + (dateObj.getMonth()+1) + "-" + dateObj.getDate();
			
			that.sdate = sdate
			that.edate = sdate
			
			
			that.getMerchantOrderLists();
			
			
		},
		onPullDownRefresh() {
			var that = this
			uni.showNavigationBarLoading();
			console.log('下拉刷新中...');
			
			that.getMerchantOrderLists();
			
			
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();
		},
		onReachBottom() {
			var that = this;
			that.getMerchantOrderLists();
		},
		methods: {
			bindSDateChange: function(e) {
				var that = this;
				that.sdate = e.target.value
				that.getMerchantOrderLists();
			},
			bindEDateChange: function(e) {
				var that = this;
				that.edate = e.target.value
				that.getMerchantOrderLists();
			},
			//显示订单信息
			showSubinfo(e){
				var that = this
				var index = e.currentTarget.dataset.index
				console.log(index)
				that.subindex = index == that.subindex ? -1 : index
			},
			//获取商户订单列表
			getMerchantOrderLists(){
				var that = this
				var uid = uni.getStorageSync('userid');
				var shopInfo = uni.getStorageSync('shopInfo');
				var shop_id = shopInfo.shop_id
				var dtype = that.dtype
				
				console.log("shop_id",shop_id)
				uni.request({
					url: common.sjApiUrl + 'merchant/getMerchantOrderInfoLists',
					method: 'GET',
					data: {
						shop_id:shop_id,
						sdate:that.sdate,
						edate:that.edate,
					},
					success: res => {
						console.log("res",res)
						that.orderList = res.data.order_list
						that.orderNums = res.data.order_nums
						that.orderAmounts = res.data.order_amounts
						that.orderJiesuanAmounts = res.data.order_jiesuan_amounts
					},
					fail: () => {},
					complete: () => {}
				});
			},
		},
	}
